.input {
  width: 350px;
  height: 80px;
  background: #ffffff;
  border: 1px solid rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 30px rgb(255 127 80 / 50%);
  border-radius: 10px;
  padding: 5px 20px;
  font-family: "Mulish";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 23px;
  overflow-y: auto;
  margin-bottom: 20px;
}

.input_dark {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.input:focus-visible {
  outline: none;
  border: 1px solid #bd4934;

  box-shadow: 0px 0px 30px rgba(182, 59, 14, 0.692);
}

.input_dark:focus-visible {
  outline: none;
  border: 1px solid #331579;

  box-shadow: 0px 0px 30px rgba(176, 68, 219, 0.5);
}
